import React, {useEffect} from 'react';
import {useForm} from "react-hook-form";
// generate random ID for order (based on source from: github)
var ID = function () {
    return 'FOOTBALLFAS_' + (Math.random().toString(36).substr(2, 9)).toUpperCase();
};
const CheckoutFormContactShipping = (props) => {
    const {changeFormType, updateOrderID, updateOrderInfo, orderInfo} = props;
    const { register, handleSubmit, errors } = useForm();
    const onSubmit = (data, ev) => {
        changeFormType(ev, "payment");
        updateOrderID(ID());
    };
    useEffect(() => {
        // toggle to placeholder
        const fields = document.querySelectorAll(".field-wrapper .field");
        // when filling
        for (let i = 0; i < fields.length; i ++) {
            fields[i].addEventListener("keyup", (ev) => {
                const fieldTopUp = fields[i].parentNode.querySelector(".field-top-up");
                if (fieldTopUp !== undefined) {
                    if (fields[i].value !== "") {
                        fields[i].style.height = "60%";
                        fieldTopUp.style.display = "block";
                    } else {
                        fields[i].style.height = "100%";
                        fieldTopUp.style.display = "none";
                    }
                }
            })
        }
        // or when input already had value
        for (let i = 0; i < fields.length; i ++) {
            const fieldTopUp = fields[i].parentNode.querySelector(".field-top-up");
                if (fieldTopUp !== undefined) {
                    if (fields[i].value !== "") {
                        fields[i].style.height = "60%";
                        fieldTopUp.style.display = "block";
                    } else {
                        fields[i].style.height = "100%";
                        fieldTopUp.style.display = "none";
                    }
                }
        }
    })
    return (
        <div className="form-contact-shipping-wrapper">
            <form 
                className="form form-contact-shipping"
                onSubmit={handleSubmit(onSubmit)}
                noValidate
            >
                {/* contact information: name, phone, email, tel */}
                <span className="title">Thng tin lin h</span>
                <div className="name-show-768" style={{display: "none"}}>
                    {/* last name */}
                    <div className="field-wrapper-container">
                        <span className="field-wrapper">
                            <input 
                                ref={register({required: true})} 
                                name="last_name" className="field" type="text" placeholder="H *" 
                                value={orderInfo.last_name}
                                onChange={(ev)=>updateOrderInfo(ev, "last_name")}
                            />
                            <span className="field-top-up">H *</span>
                        </span>
                        <span className="error-message">
                            {errors.last_name && "Vui lng nhp H"}
                        </span>
                    </div>
                    {/* first name */}
                    <div className="field-wrapper-container">
                        <span className="field-wrapper">
                            <input 
                                ref={register({required: true})} 
                                name="first_name" className="field" type="text" placeholder="Tn m v tn *" 
                                onChange={(ev)=>updateOrderInfo(ev, "first_name")}
                                value={orderInfo.first_name}
                            />
                            <span className="field-top-up">Tn m v tn *</span>
                        </span>
                        <span className="error-message">
                            {errors.first_name && "Vui lng nhp Tn m v tn"}
                        </span>
                    </div>
                </div>
                {/* last name */}
                <span className="field-wrapper hide-768">
                    <input 
                        ref={register({required: true})} 
                        name="last_name" className="field" type="text" placeholder="H *" 
                        value={orderInfo.last_name}
                        onChange={(ev)=>updateOrderInfo(ev, "last_name")}
                    />
                    <span className="field-top-up">H *</span>
                </span>
                <span className="error-message hide-768">
                    {errors.last_name && "Vui lng nhp H"}
                </span>
                {/* first name */}
                <span className="field-wrapper hide-768">
                    <input 
                        ref={register({required: true})} 
                        name="first_name" className="field" type="text" placeholder="Tn m v tn *" 
                        onChange={(ev)=>updateOrderInfo(ev, "first_name")}
                        value={orderInfo.first_name}
                    />
                    <span className="field-top-up">Tn m v tn *</span>
                </span>
                <span className="error-message hide-768">
                    {errors.first_name && "Vui lng nhp Tn m v tn"}
                </span>
                <div className="name-show-768" style={{display: "none"}}>
                    {/* email */}
                    <div className="field-wrapper-container">
                        <span className="field-wrapper">
                            <input 
                                ref={register({pattern: /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/, required: true})} 
                                name="email" className="field" type="email" placeholder="Email" 
                                onChange={(ev)=>updateOrderInfo(ev, "email")}
                                value={orderInfo.email}
                            />
                            <span className="field-top-up">Email</span>
                        </span>
                        <span className="error-message">
                            {errors.email && "Vui lng nhp ng nh dng email"}
                        </span>
                    </div>
                    {/* phone */}
                    <div className="field-wrapper-container">
                        <span className="field-wrapper">
                            <input 
                                ref={register({required: true})} 
                                name="tel" className="field" type="tel" placeholder="in thoi *" 
                                onChange={(ev)=>updateOrderInfo(ev, "tel")}
                                value={orderInfo.tel}
                            />
                            <span className="field-top-up">in thoi *</span>
                        </span>
                        <span className="error-message">
                            {errors.tel && "Vui lng nhp s in thoi"}
                        </span>
                    </div>
                </div>
                {/* email */}
                <span className="field-wrapper hide-768">
                    <input 
                        ref={register({pattern: /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/, required: true})} 
                        name="email" className="field" type="email" placeholder="Email" 
                        onChange={(ev)=>updateOrderInfo(ev, "email")}
                        value={orderInfo.email}
                    />
                    <span className="field-top-up">Email</span>
                </span>
                <span className="error-message hide-768">
                    {errors.email && "Vui lng nhp ng nh dng email"}
                </span>
                {/* phone */}
                <span className="field-wrapper hide-768">
                    <input 
                        ref={register({required: true})} 
                        name="tel" className="field" type="tel" placeholder="in thoi *" 
                        onChange={(ev)=>updateOrderInfo(ev, "tel")}
                        value={orderInfo.tel}
                    />
                    <span className="field-top-up">in thoi *</span>
                </span>
                <span className="error-message hide-768">
                    {errors.tel && "Vui lng nhp s in thoi"}
                </span>
                {/* shipping address */}
                <span className="title">a ch nhn hng</span>
                {/* city */}
                <span className="field-wrapper">
                    <input 
                        ref={register({required: true})} 
                        name="city" className="field" type="text" placeholder="Tnh / thnh ph *" 
                        onChange={(ev)=>updateOrderInfo(ev, "city")}
                        value={orderInfo.city}
                    />
                    <span className="field-top-up">Tnh / thnh ph *</span>
                </span>
                <span className="error-message">
                    {errors.city && "Vui lng nhp Tnh / thnh ph"}
                </span>
                {/* district */}
                <span className="field-wrapper">
                    <input 
                        ref={register({required: true})} 
                        name="district" className="field" type="text" placeholder="Qun / huyn *" 
                        onChange={(ev)=>updateOrderInfo(ev, "district")}
                        value={orderInfo.district}
                    />
                    <span className="field-top-up">Qun / huyn *</span>
                </span>
                <span className="error-message">
                    {errors.district && "Vui lng nhp Qun / huyn"}
                </span>
                {/* ward */}
                <span className="field-wrapper">
                    <input 
                        ref={register({required: true})} 
                        name="ward" className="field" type="text" placeholder="X / phng/ th trn *" 
                        onChange={(ev)=>updateOrderInfo(ev, "ward")}
                        value={orderInfo.ward}
                    />
                    <span className="field-top-up">X / phng/ th trn *</span>
                </span>
                <span className="error-message">
                    {errors.ward && "Vui lng nhp X / phng/ th trn"}
                </span>
                {/* street */}
                <span className="field-wrapper">
                    <input 
                        ref={register({required: true})} 
                        name="street" className="field" type="text" placeholder="Tn ng *" 
                        onChange={(ev)=>updateOrderInfo(ev, "street")}
                        value={orderInfo.street}
                    />
                    <span className="field-top-up">Tn ng *</span>
                </span>
                <span className="error-message">
                    {errors.street && "Vui lng nhp Tn ng"}
                </span>
                {/* house_no */}
                <span className="field-wrapper">
                    <input 
                        ref={register({required: true})} 
                        name="house_no" className="field" type="text" placeholder="S nh *" 
                        onChange={(ev)=>updateOrderInfo(ev, "house_no")}
                        value={orderInfo.house_no}
                    />
                    <span className="field-top-up">S nh *</span>
                </span>
                <span className="error-message">
                    {errors.house_no && "Vui lng nhp S nh"}
                </span>
                <input type="submit" value="Thanh ton"/>
            </form>
        </div>
    );
};

export default CheckoutFormContactShipping;